export default {
	onReady: function(res) {

		this.videoContext = uni.createVideoContext('myVideo')

	},
	data() {
		return {
			curid: '',
			danmuList: [{
					text: '第 1s 出现的弹幕',
					color: '#ff0000',
					time: 1
				},
				{
					text: '第 3s 出现的弹幕',
					color: '#ff00ff',
					time: 3
				}
			],
			danmuValue: '',
			videolist: [{
					curid: '1',
					src: "http://192.168.0.106:8000/test.mp4"
				},
				{
					curid: '2',
					src: '/static/222.mp4'

				},
				{
					curid: '3',
					src: "/static/333.mp4"
				}
			]
		}
	},
	onLoad: function(e) {
		console.log(e);
		this.curid = e.id;
		console.log(this.curid)
	},
	methods: {
		sendDanmu: function() {
			this.videoContext.sendDanmu({
				text: this.danmuValue,
				color: this.getRandomColor()
			});
			this.danmuValue = '';
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg,
				showCancel: false
			})
		},
		getRandomColor: function() {
			const rgb = []
			for (let i = 0; i < 3; ++i) {
				let color = Math.floor(Math.random() * 256).toString(16)
				color = color.length == 1 ? '0' + color : color
				rgb.push(color)
			}
			return '#' + rgb.join('')
		}
	}
}
